import React from "react";
import Player from "./Diya_player";
import './scss/Eduzone_playlist.scss';
import $ from 'jquery';
class Playlist extends React.Component {
    openCity(cityName) {
        // Declare all variables
        var i, tabcontent, tablinks;

        // Get all elements with class="tabcontent" and hide them
        tabcontent = document.getElementsByClassName("tabcontent");
        for (i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }

        // Get all elements with class="tablinks" and remove the class "active"
        tablinks = document.getElementsByClassName("tablinks");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }

        // Show the current tab, and add an "active" class to the button that opened the tab
        document.getElementById(cityName).style.display = "block";
    }
    componentDidMount() {
        this.myfuction()
        this.videometer()
    }
    async myfuction() {
        await $.getJSON(window.json_server + "article/" + this.props.match.params.uid, function (data) {
            var jsons = data.data
            console.log(data)
            $('#jquey_video').show();
            $('.jquery_diya_load').hide();
            $('#jquery_vname').text(jsons.name);
            $('#jquery_vinfo').text(jsons.info);
            if (jsons.article === true) {
                $('#jquery_tabs').show();
                $('#jquery_article').html(jsons.content)
            }
            $('#jquery_qa .commentList').html(jsons.comments)
        })
    }
    comment(vid) {
        $('.comment-box').hide();
        $('#commentload').show();
        var comment_text = $('#jquery_qa .comment-box textarea').val()
        $.ajax({
            type: "POST",
            url: window.json_server + 'comment/' + vid + '?tempuser=' + window.tempuser + '&text=' + comment_text,
            success: function (data) {
                $('#jquery_qa .commentList').append(data);
                $('#jquery_qa .comment-box textarea').val(' ');
                $('#commentload').hide();
                $('.comment-box').show("slow");
            },
            error: function (data) {
                $('.comment-box').show();
                $('#jquery_qa .commentList').append(data)
                $('.#commentload').hide();
            },
        });
    }
    async videometer(){
        $.ajax({
            url: window.json_server + 'watched/?tempuser=' + window.tempuser + '&url='+encodeURI(window.location.href)
          });
    }
    render() {
        return (
            <div>
                <Player video={atob(this.props.match.params.youtube_uri)} />
                <div className="jquery_diya_load diya_load_gif" style={{display:'block'}}></div>
                <div id="jquey_video" style={{ display: 'none' }}>
                    <div style={{ marginLeft: '15px', marginTop: '10px' }}>
                        <h2 id="jquery_vname"> </h2>
                        <h5 id="jquery_vinfo">Video Description</h5>
                    </div>
                    <div id="jquery_tabs" style={{ display: 'none' }} className="eduzone_tabs">
                        <div className="tab">
                            <button className="tablinks" onClick={() => this.openCity('jquery_article')}>Notes</button>
                            <button className="tablinks" onClick={() => this.openCity('jquery_qa')}>QA</button>
                        </div>
                        <div id="jquery_article" className="tabcontent" style={{ display: 'block' }}>
                        </div>
                        <div id="jquery_qa" className="tabcontent">
                            <div className="comment-body">
                                <ul className="commentList">

                                </ul>
                            </div>
                            <div id="commentload" className="diya_load_gif"></div>
                            <div className="comment-box">
                                <textarea id="#mastercomment"  placeholder="Ask/Comment  here...">
                                    
                                </textarea>
                                <div>
                                    <button onClick={() => { this.comment(this.props.match.params.uid) }}>Sent</button>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Playlist;