import React from 'react';

import { Route, Switch } from 'react-router-dom';
import Categories from './Eduzone_cat';
import Diyaads from './Diya_ads';
import Channel from "./Eduzone_channel";
import Programmes from "./Eduzone_programmes";
import Playlist from "./Eduzone_playlist";
import { NotfoundError } from './Diya_error'
import Profile from './Eduzone_profile'
import MyCourses from './Eduzone_courses'
class Routes extends React.Component {
    render() {

        return (
            <Switch>
                <Route exact path='/'
                    render={function () {
                        return (
                            <div>
                                <Diyaads />
                                <Categories />
                            </div>
                        )
                    }} />
                <Route exact path="/static"><h1 style={{ textAlign: 'center' }}><a href="/classroom" title="">Go To Classroom here</a></h1></Route>
                <Route exact path="/courses" component={MyCourses}></Route>
                <Route exact path="/profile" component={Profile}></Route>
                <Route exact path="/payments" component={Profile}></Route>
                <Route exact path='/channel/:id' component={Channel}></Route>
                <Route exact path='/channel/:id/programme/:pgm' component={Programmes}></Route>
                <Route exact path='/channel/:id/programme/:pgm/u/:youtube_uri/:uid' component={Playlist}></Route>
                <Route
                    render={function () {
                        return (
                            <div><NotfoundError error={" Error : 404 The Page '" + window.location.pathname + "' was Not Found in this Application"} /></div>
                        )
                    }} />
            </Switch>
        );
    }
}

export default Routes;