import React, { useEffect } from "react";
import "./scss/Diya_player.scss";
import $ from 'jquery';
var jQuery = $
function Player(video) {
    useEffect(function () {

        $('.loader').hide();
        window['YT'] = { loading: 0, loaded: 0 };
        window['YTConfig'] = { 'host': 'http://localhost:3000' };

        window.YT.loading = 1;
        (function () {
            console.log("event fired");
            var l = [];
            window.YT.ready = function (f) {
                if (window.YT.loaded) { f(); } else {
                    l.push(f);
                }
            };
            window.onYTReady = function () {
                window.YT.loaded = 1;
                for (var i = 0; i < l.length; i++) {
                    try {
                        l[i]();
                    }
                    catch (e) { }
                }
            };
            window.YT.setConfig = function (c) {
                for (var k in c) {
                    if (c.hasOwnProperty(k)) {
                        window.YTConfig[k] = c[k];
                    }
                }
            };
            var a = document.createElement('script');
            a.type = 'text/javascript'; a.id = 'www-widgetapi-script';
            a.src = 'https://s.ytimg.com/yts/jsbin/www-widgetapi-vflkJY3IF/www-widgetapi.js';
            a.async = true;
            var c = document.currentScript;
            if (c) {
                var n = c.nonce || c.getAttribute('nonce');
                if (n) {
                    a.setAttribute('nonce', n);
                }
            }
            var b = document.getElementById('youtubescriptarea');
            b.innerHTML = "";
            b.append(a);
        })();

        /**
* @preserve jquery.fullscreen 1.1.5
* https://github.com/code-lts/jquery-fullscreen-plugin
* Copyright (C) 2012-2013 Klaus Reimer <k@ailis.de>
* Licensed under the MIT license
* (See http://www.opensource.org/licenses/mit-license)
*/

        (function (jQuery) {

            /**
             * Sets or gets the fullscreen state.
             *
             * @param {boolean=} state
             *            True to enable fullscreen mode, false to disable it. If not
             *            specified then the current fullscreen state is returned.
             * @return {boolean|Element|jQuery|null}
             *            When querying the fullscreen state then the current fullscreen
             *            element (or true if browser doesn't support it) is returned
             *            when browser is currently in full screen mode. False is returned
             *            if browser is not in full screen mode. Null is returned if
             *            browser doesn't support fullscreen mode at all. When setting
             *            the fullscreen state then the current jQuery selection is
             *            returned for chaining.
             * @this {jQuery}
             */
            function fullScreen(state) {
                var e, func, doc;

                // Do nothing when nothing was selected
                if (!this.length) return this;

                // We only use the first selected element because it doesn't make sense
                // to fullscreen multiple elements.
                e = (/** @type {Element} */ this[0]);

                // Find the real element and the document (Depends on whether the
                // document itself or a HTML element was selected)
                if (e.ownerDocument) {
                    doc = e.ownerDocument;
                }
                else {
                    doc = e;
                    e = doc.documentElement;
                }

                // When no state was specified then return the current state.
                if (state == null) {
                    // When fullscreen mode is not supported then return null
                    if (!((/** @type {?Function} */ doc["exitFullscreen"])
                        || (/** @type {?Function} */ doc["webkitExitFullscreen"])
                        || (/** @type {?Function} */ doc["webkitCancelFullScreen"])
                        || (/** @type {?Function} */ doc["msExitFullscreen"])
                        || (/** @type {?Function} */ doc["mozCancelFullScreen"]))) {
                        return null;
                    }

                    // Check fullscreen state
                    state = fullScreenState(doc);
                    if (!state) return state;

                    // Return current fullscreen element or "true" if browser doesn't
                    // support this
                    return (/** @type {?Element} */ doc["fullscreenElement"])
                        || (/** @type {?Element} */ doc["webkitFullscreenElement"])
                        || (/** @type {?Element} */ doc["webkitCurrentFullScreenElement"])
                        || (/** @type {?Element} */ doc["msFullscreenElement"])
                        || (/** @type {?Element} */ doc["mozFullScreenElement"])
                        || state;
                }

                // When state was specified then enter or exit fullscreen mode.
                if (state) {
                    // Enter fullscreen
                    func = (/** @type {?Function} */ e["requestFullscreen"])
                        || (/** @type {?Function} */ e["webkitRequestFullscreen"])
                        || (/** @type {?Function} */ e["webkitRequestFullScreen"])
                        || (/** @type {?Function} */ e["msRequestFullscreen"])
                        || (/** @type {?Function} */ e["mozRequestFullScreen"]);
                    if (func) {
                        func.call(e);
                    }
                    return this;
                }
                else {
                    // Exit fullscreen
                    func = (/** @type {?Function} */ doc["exitFullscreen"])
                        || (/** @type {?Function} */ doc["webkitExitFullscreen"])
                        || (/** @type {?Function} */ doc["webkitCancelFullScreen"])
                        || (/** @type {?Function} */ doc["msExitFullscreen"])
                        || (/** @type {?Function} */ doc["mozCancelFullScreen"]);
                    if (func && fullScreenState(doc)) func.call(doc);
                    return this;
                }
            }

            /**
             * Check fullscreen state
             *
             * @param {Document} doc The content document
             * @return {Boolean}
             */
            function fullScreenState(doc) {
                return !!(doc["fullscreenElement"] || doc["msFullscreenElement"] || doc["webkitIsFullScreen"] || doc["mozFullScreen"]);
            }

            /**
             * Toggles the fullscreen mode.
             *
             * @return {!jQuery}
             *            The jQuery selection for chaining.
             * @this {jQuery}
             */
            function toggleFullScreen() {
                return (/** @type {!jQuery} */ fullScreen.call(this,
                    !fullScreen.call(this)));
            }

            /**
             * Handles the browser-specific fullscreenchange event and triggers
             * a jquery event for it.
             *
             * @param {?Event} event
             *            The fullscreenchange event.
             */
            function fullScreenChangeHandler(event) {
                jQuery(document).trigger(new jQuery.Event("fullscreenchange"));
            }

            /**
             * Handles the browser-specific fullscreenerror event and triggers
             * a jquery event for it.
             *
             * @param {?Event} event
             *            The fullscreenerror event.
             */
            function fullScreenErrorHandler(event) {
                jQuery(document).trigger(new jQuery.Event("fullscreenerror"));
            }

            /**
             * Installs the fullscreenchange event handler.
             */
            function installFullScreenHandlers() {
                var e, change, error;

                // Determine event name
                e = document;
                if (e["webkitCancelFullScreen"]) {
                    change = "webkitfullscreenchange";
                    error = "webkitfullscreenerror";
                }
                else if (e["msExitFullscreen"]) {
                    change = "MSFullscreenChange";
                    error = "MSFullscreenError";
                }
                else if (e["mozCancelFullScreen"]) {
                    change = "mozfullscreenchange";
                    error = "mozfullscreenerror";
                }
                else {
                    change = "fullscreenchange";
                    error = "fullscreenerror";
                }

                // Install the event handlers
                jQuery(document).on(change, fullScreenChangeHandler);
                jQuery(document).on(error, fullScreenErrorHandler);
            }

            jQuery.fn["fullScreen"] = fullScreen;
            jQuery.fn["toggleFullScreen"] = toggleFullScreen;
            installFullScreenHandlers();

        })(jQuery);
    }, []);
    var a;
    var b;
    var c = video.video;
    var protocol = window.location.protocol;
    var slashes = protocol.concat("//");
    var host = slashes.concat(window.location.hostname);
    console.log(c);
    a = $(window).width();
    if (a > 900) {
        a = 900;
    }
    b = a * (9 / 16);
    var player;
    window['onYouTubeIframeAPIReady'] = async function () {
        player = new window.window.YT.Player('player', {
            height: a,
            width: b,
            videoId: c,
            playerVars: {
                'autoplay': 1,
                'controls': 0,
                showinfo: 0,
                rel: 0,
                disablekb: 1,
                modestbranding: 1,
                wmode: 'opaque',
                origin: host,
                data: 'small',
                theme: 'dark'
            },
            events: {
                'onReady': onPlayerReady,
                'onStateChange': onPlayerStateChange
            }
        });
    }
    function onPlayerReady(event) {
        $('.diyavideoplayer').show();
        //$('#player').hide();
        $('.loader').hide();
        $(".diyapause").hide();
        event.target.playVideo();
        _diyau2beplayer_play()
        setTimeout(_diyau2beplayer_play(), 3000)
    }
    function _diyau2beplayer_play() {
        player.playVideo();
    }

    function _diyau2beplayer_pause() {
        player.pauseVideo();
    }

    function _diyau2beplayer_seek_forward() {
        player.seekTo(player.getCurrentTime() + 20, true)
    }
    function _diyau2beplayer_seek_backward() {
        player.seekTo(player.getCurrentTime() - 10, true)
    }
    function _diyau2beplayer_seek_stop() {
        player.stopVideo();
    }
    function onPlayerStateChange(event) {
        if (event.data === 2) {

            $('.loader').hide();
            $('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === 5) {

            $('.loader').hide();
            $('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === 0) {

            $('.loader').hide();
            $('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === -1) {

            $('.loader').hide();
            //$('#player').hide();
            $(".diyaplay").show();
            $(".diyapause").hide();

        } else if (event.data === 1) {
            $('#player').show();
            $('.loader').hide();
            $(".diyaplay").hide();
            $(".diyapause").show();

        } else if (event.data === 3) {
            $('.loader').show();
        } else {
            console.log(event);
        }
    }
    return (
        <div>
            <div className="diyavideoplayer" style={{ height: b }}>
                <div className="bg"></div>
                <div className="player" id="player"></div>
                <div className="loader"><img alt="" src="https://i.ya-webdesign.com/images/loading-gif-png-5.gif" /></div>
                <div className="overlay"></div>
                <div className="overlaycontrols">
                    <button onClick={_diyau2beplayer_seek_backward}> <i className="fa fa-backward"></i></button>
                    <button className="diyaplay" onClick={_diyau2beplayer_play}><i className="fa fa-play"></i></button>
                    <button style={{ display: 'none' }} className="diyapause" onClick={_diyau2beplayer_pause}><i className="fa fa-pause"></i></button>
                    <button style={{ display: 'block' }} className="diyastop" onClick={_diyau2beplayer_seek_stop}><i className="fa fa-stop"></i></button>
                    <button onClick={_diyau2beplayer_seek_forward}><i className="fa fa-forward"></i></button>
                    <button onClick={fullscreener}><i className="fa fa-arrows-alt"></i></button>
                </div>

            </div>
        </div>
    )
    function fullscreener() {
        $('.diyavideoplayer').toggleFullScreen();
        console.log("Sample event")
    }
}
export default Player;