import React from 'react';
import "./scss/Eduzone_programmes.scss";
import { Link } from "react-router-dom";
import { NotfoundError, Servererror } from './Diya_error'
class Programmes extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
            channel: this.props.match.params.id,
            programme: this.props.match.params.pgm,
            data: [],
            demo: false,
            error_msg: '',
            width: 1,
        }
        this.loadepage = this.loadepage.bind(this);
        this.read_programmes = this.read_programmes.bind(this);
        this.newpage = this.newpage.bind(this);
        this.oldpages = this.oldpages.bind(this);
    }
    componentDidMount() {
        this.read_programmes(1)
    }
    a = 1;
    async read_programmes(letter) {
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false
        })
        await fetch(window.json_server + this.state.channel + "/programme/" + this.state.programme + "?p=" + letter, {
            'cache': 'no-store'
        })
            .then(response => response.json())
            .then((jsonData) => {
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
                if (jsonData.response === 200) {
                    // jsonData is parsed json object received from url
                    this.setState({
                        data: jsonData.data,
                        width: jsonData.width,
                        data_loaded: true,
                        ajax_done: true,
                        demo: jsonData.demo
                    })
                }
                else if (jsonData.response === 404) {
                    // jsonData is parsed json object received from url
                    this.setState({
                        data_loaded: false,
                        ajax_done: true,
                        ajax_error: false,
                        error_msg: jsonData.message
                    })
                } else {
                    window.location = window.login_url + window.location.pathname
                }
            })
            .catch((error) => {
                this.setState({
                    data_loaded: false,
                    ajax_done: true,
                    ajax_error: true,
                })
                console.log(error)
            });
    }
    loadepage() {
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
        })
        this.read_programmes(1)
    }
    newpage(letter) {
        this.a = letter
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false
        })
        this.read_programmes(this.a)
    }
    oldpages(letter) {
        this.a = letter
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
        })
        this.read_programmes(this.a)
    }
    hellopay(url) {
        var win = window.open(url, "Payment SO", "config='toolbar=no, menubar=no,scrollbars=no,resizable=no,location=no,directories=no,status=no'");

        var timer = setInterval(function () {
            if (win.closed) {
                clearInterval(timer);
                alert('Payment window Closed');
                window.location.reload()
            }
        }, 1000);
    }
    render() {
        return (
            <div>
                {this.state.ajax_error === false ?
                    <div className="date_slider">
                        <div className="row">
                            <div className="date_slide col-3 col-md-3 col-lg-3">
                                <button onClick={this.loadepage}>Latest</button>
                            </div>
                            <div className="date_slide col-3 col-md-3  col-lg-3">
                                <button><i className="fa fa-arrows-h"></i></button>
                            </div>

                            <div className="date_slide col-3 col-md-3  col-lg-3">
                                {this.a > 1 ? <button onClick={() => this.newpage(this.a - 1)}><i className="fa fa-angle-double-left"></i> Newer</button> : null}
                            </div>
                            <div className="date_slide col-3  col-md-3  col-lg-3">
                                {this.state.width !== 0 && this.state.width > 9 ? <button onClick={() => this.oldpages(this.a + 1)}>Older <i className="fa fa-angle-double-right"></i></button> : null}
                            </div>
                        </div>
                    </div>
                    : null}
                {this.state.ajax_done === true && this.state.data_loaded === true ?
                    <div className="programme_sheet">
                        {this.state.data_loaded === true ?
                            <div className="name_set">
                                {this.state.data.map(programme => {
                                    return (
                                        <a key={programme.name} href="#a">
                                            <div className="item">{programme.name}</div>
                                        </a>)
                                })}
                            </div>
                            : null}
                        {this.state.data_loaded === true ?
                            <div className="programme_set">
                                {this.state.demo === true ?
                                    <div className="banner" style={{ width: this.state.width * 335 }}>

                                        {this.state.data.map(conent => {
                                            return (
                                                <div className="item" key={conent.name}>
                                                    {conent.playlist.filter(({ demo }) => demo === "True").map(playlist => {
                                                        return (
                                                            <div key={playlist.date}>
                                                                <Link to={"/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" + btoa(playlist.uri) + "/" + playlist.uid}>
                                                                    <div className="item-box">
                                                                        <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                        <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                    </div>
                                                                </Link>
                                                            </div>
                                                        )
                                                    })}
                                                    {conent.playlist.filter(({ demo }) => demo === "False").map(playlist => {
                                                        return (
                                                            <div key={playlist.date}>
                                                                <a href="#pay" onClick={() => this.hellopay(playlist.uri)} title="order a plan">
                                                                    <div className="item-box demo">
                                                                        <i className="fa fa-lock"></i>
                                                                        <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                        <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                    </div>
                                                                </a>
                                                            </div>
                                                        )
                                                    })}
                                                </div>
                                            )
                                        })}

                                    </div> :
                                    <div className="banner" style={{ width: this.state.width * 338 }}>
                                        {this.state.data.map(conent => {
                                            return (
                                                <div className="item">
                                                    {conent.playlist.map(playlist => {
                                                        return (
                                                            <div key={playlist}>
                                                                <Link to={"/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" + btoa(playlist.uri) + "/" + playlist.uid}>
                                                                    <div className="item-box">
                                                                        <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                        <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                    </div>
                                                                </Link>
                                                            </div>

                                                        )
                                                    })}

                                                </div>
                                            )
                                        })}
                                    </div>
                                }
                            </div>
                            : null}
                    </div>

                    : null}
                {this.state.ajax_done === false ? <img className="diya_load" alt="" src="https://i.ya-webdesign.com/images/loading-gif-png-5.gif" /> : null}
                {this.state.ajax_done === true && this.state.data_loaded === false && this.state.ajax_error === false ?
                    <NotfoundError error={this.state.error_msg} /> : null}
                {this.state.ajax_done === true && this.state.ajax_error === true ?
                    <Servererror error={this.state.error_msg} /> : null}
            </div>
        )
    }
}
export default Programmes;