import React from 'react'
import './scss/Eduzone_api.scss'
class MyCourses extends React.Component{
    componentDidMount(){
        window.$('eduzone_profile').load(window.profile_url+'courses')
    }
    render(){
        return(
            <div id="eduzone_profile" className="eduzone_api">

            </div>
        )
    }
}
export default MyCourses;